.membership-page {
  min-height: 100vh;
}

/* Page Header */
.page-header {
  background: linear-gradient(135deg, #FF6B35, #F7931E);
  color: white;
  padding: 80px 0;
  text-align: center;
}

.page-header h1 {
  font-size: 3rem;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.page-header p {
  font-size: 1.3rem;
  opacity: 0.95;
}

/* Benefits Section */
.benefits-section {
  padding: 80px 0;
  background: #f9f9f9;
}

.section-header {
  text-align: center;
  margin-bottom: 60px;
}

.section-header h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 15px;
}

.underline {
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, #FF6B35, #F7931E);
  margin: 0 auto;
  border-radius: 2px;
}

.benefits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.benefit-card {
  background: white;
  padding: 40px 30px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s;
  border: 2px solid transparent;
}

.benefit-card:hover {
  transform: translateY(-10px);
  border-color: #FF6B35;
  box-shadow: 0 10px 30px rgba(255, 107, 53, 0.15);
}

.benefit-icon {
  font-size: 4rem;
  margin-bottom: 20px;
}

.benefit-card h3 {
  color: #FF6B35;
  margin-bottom: 15px;
  font-size: 1.4rem;
}

.benefit-card p {
  color: #666;
  line-height: 1.6;
}

/* Registration Section */
.registration-section {
  padding: 80px 0;
  background: white;
}

.form-container {
  max-width: 900px;
  margin: 0 auto;
  background: white;
  padding: 50px;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.form-container h2 {
  color: #333;
  font-size: 2rem;
  margin-bottom: 15px;
  text-align: center;
}

.form-description {
  text-align: center;
  color: #666;
  margin-bottom: 40px;
  font-size: 1.1rem;
}

.registration-form {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  color: #333;
  font-weight: 600;
  margin-bottom: 10px;
  font-size: 1rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 15px;
  border: 2px solid #e0e0e0;
  border-radius: 10px;
  font-size: 1rem;
  transition: border-color 0.3s;
  font-family: inherit;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #FF6B35;
}

.form-group textarea {
  resize: vertical;
}

.form-group select {
  cursor: pointer;
  background: white;
}

.form-actions {
  text-align: center;
  margin-top: 20px;
}

.submit-btn {
  background: linear-gradient(135deg, #FF6B35, #F7931E);
  color: white;
  border: none;
  padding: 18px 60px;
  border-radius: 30px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
}

.submit-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(255, 107, 53, 0.3);
}

/* Responsive Design */
@media (max-width: 768px) {
  .page-header h1 {
    font-size: 2rem;
  }

  .page-header p {
    font-size: 1.1rem;
  }

  .section-header h2 {
    font-size: 2rem;
  }

  .form-container {
    padding: 30px 20px;
  }

  .form-container h2 {
    font-size: 1.5rem;
  }

  .benefits-grid {
    grid-template-columns: 1fr;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .submit-btn {
    width: 100%;
  }
}
