.footer {
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  color: white;
  margin-top: 60px;
  width: 100%;
  overflow-x: hidden;
}

.footer-content {
  padding: 60px 0 30px;
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  width: 100%;
}

.footer-section h3 {
  color: #FF6B35;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.footer-section h4 {
  color: #F7931E;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.footer-section p {
  line-height: 1.8;
  color: #ddd;
  margin-bottom: 20px;
}

.social-icons {
  display: flex;
  gap: 15px;
  font-size: 1.5rem;
}

.social-icons a {
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  min-width: 44px;
  min-height: 44px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.social-icons a:hover {
  transform: translateY(-3px);
  background-color: rgba(255, 107, 53, 0.1);
}

.social-icons a:focus {
  outline: 2px solid white;
  outline-offset: 2px;
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-section ul li {
  margin-bottom: 12px;
}

.footer-section ul li a {
  color: #ddd;
  text-decoration: none;
  transition: color 0.2s ease, padding-left 0.2s ease;
  display: inline-block;
  padding: 0.25rem 0;
  min-height: 44px;
  display: inline-flex;
  align-items: center;
}

.footer-section ul li a:hover {
  color: #FF6B35;
  padding-left: 8px;
}

.footer-section ul li a:focus {
  outline: 2px solid white;
  outline-offset: 2px;
  border-radius: 4px;
}

.contact-details p {
  margin-bottom: 12px;
  color: #ddd;
}

.footer-bottom {
  background: rgba(0, 0, 0, 0.3);
  padding: 20px 0;
  margin-top: 30px;
}

.footer-bottom-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.footer-bottom-content p {
  margin: 0;
  color: #ddd;
}

.footer-links {
  display: flex;
  gap: 20px;
}

.footer-links a {
  color: #ddd;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-links a:hover {
  color: #FF6B35;
}

@media (max-width: 768px) {
  .footer {
    margin-top: 40px;
  }

  .footer-content {
    padding: 40px 0 20px;
  }

  .footer-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .footer-section h3 {
    font-size: 1.3rem;
  }

  .footer-section h4 {
    font-size: 1.1rem;
  }

  .footer-bottom {
    padding: 15px 0;
  }

  .footer-bottom-content {
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }

  .footer-section {
    text-align: center;
  }

  .social-icons {
    justify-content: center;
  }

  .footer-links {
    flex-direction: column;
    gap: 10px;
  }
}

@media (max-width: 480px) {
  .footer-section h3 {
    font-size: 1.2rem;
  }

  .footer-section p,
  .contact-details p {
    font-size: 0.95rem;
  }
}
